
import axios from "axios";
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Flexbox,
FlexboxItem,
ViewBox,
Group,
Cell,
XButton,
XDialog,
XTextarea,
Previewer
},
props: ["userId", "Id", "name", "departmentId"],
data() {
return {
currItem: null,
rejectReason: "",
showRejectDialog: false,
list: [],
totalscore:0.0,
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
};
},

computed: {
userName() {
return decodeURIComponent(this.name);
}
},
mounted() {
this.getInfo();
this.getlist();
},
methods: {
getInfo() {
axios.get('ppartymember/queryByUserId', {
params: {
userid: this.userId
}
})
.then(res => {
console.log(res);
this.totalscore = res.data.totalscore || 0.0;
})
.catch(err => {
console.log(err);
});
},
atBig (index,i) {
console.log(this.$refs);
this.$refs.previewer[i].show(index);
},
getlist() {
axios({
method: "get",
url: "pavantgrade/getList",
params: {
userId: this.userId
}
})
.then(res => {
console.log(res);
this.list = res.data;
this.list.forEach(item => {
item.memo = (item.memo && item.memo.split(",")) || [];

item.memos =[];

for(var i=0;i<item.memo.length;i++){
var obj = {};
obj.msrc = 'http://dj.dlbdata.cn/dangjian/picture/showThumbnail?pictureId='+ item.memo[i];
obj.src = 'http://dj.dlbdata.cn/dangjian/picture/show?pictureId='+item.memo[i];
item.memos.push(obj);
}

});


})
.catch(function(error) {
console.log(error);
});
},
logIndexChange (arg) {
console.log(arg)
},
auditReject(item) {
this.currItem = item;
this.showRejectDialog = true;

},
auditResolve(item) {
axios({
method: "post",
url: "pavantgrade/examineOK",
params: {
id: item.id
}
})
.then(res => {
if(res.success){
this.$vux.alert.show({title: res.msg});

window.location.reload();
}else{
this.$vux.alert.show({title: '提交失败'});
}

})
.catch(function(error) {
this.$vux.alert.show({title: '提交失败'});
});
},
dialogConfirm() {
axios({
method: "post",
url: "pavantgrade/examineNo",
params: {
id: this.currItem.id,
rejectReson: this.rejectReason
}
}).then(res => {
if(res.success){
this.$vux.alert.show({title: '拒绝成功'});
window.location.reload();
}else{
this.$vux.alert.show({title: '提交失败'});
}

this.rejectReason = "";
})
.catch(function(error) {
this.$vux.alert.show({title: '提交失败'});
console.log(error);
});
this.showRejectDialog = false;
},
dialogCancel() {
this.currItem = null;
this.rejectReason = "";
this.showRejectDialog = false;
}
}
};



